<template>
  <div class="heading-block" :class="{ 'heading-block--editing': editing }">
    <div v-if="!editing" class="heading-block__view" @click="startEdit">
      <component
        :is="'h' + block.level"
        class="heading-block__text"
        :class="'heading-block__text--h' + block.level"
      >
        {{ block.header || 'Заголовок' }}
      </component>
      <button class="heading-block__menu" @click.stop="$emit('delete')">
        <v-icon icon="mdi-dots-vertical" size="18" />
      </button>
    </div>
    <div v-else class="heading-block__edit">
      <div class="heading-block__levels">
        <button
          v-for="level in 3"
          :key="level"
          class="heading-block__level"
          :class="{ 'heading-block__level--active': draftLevel === level }"
          @click="draftLevel = level"
        >
          H{{ level }}
        </button>
      </div>
      <input
        ref="input"
        v-model="draftHeader"
        type="text"
        class="heading-block__input"
        :class="'heading-block__input--h' + draftLevel"
        placeholder="Введите заголовок..."
        @blur="save"
        @keyup.enter="save"
        @keyup.escape="cancel"
      />
    </div>
  </div>
</template>

<script>
import { BlocksCall } from '@/api/call'

export default {
  name: 'HeadingBlock',
  props: {
    block: {
      type: Object,
      required: true,
    },
    pageId: {
      type: String,
      required: true,
    },
  },
  emits: ['update', 'delete'],
  data() {
    return {
      editing: false,
      draftHeader: '',
      draftLevel: 1,
    }
  },
  methods: {
    startEdit() {
      this.draftHeader = this.block.header || ''
      this.draftLevel = this.block.level || 1
      this.editing = true
      this.$nextTick(() => {
        this.$refs.input?.focus()
      })
    },
    async save() {
      if (this.draftHeader === this.block.header && this.draftLevel === this.block.level) {
        this.editing = false
        return
      }

      try {
        await BlocksCall.updateBlock(this.pageId, this.block.id, {
          type: 'HEADING',
          header: this.draftHeader,
          level: this.draftLevel,
        })
        this.$emit('update', { ...this.block, header: this.draftHeader, level: this.draftLevel })
      } catch (error) {
        console.error('Failed to update heading:', error)
      }
      this.editing = false
    },
    cancel() {
      this.editing = false
    },
  },
}
</script>

