<template>
  <Teleport to="body">
    <Transition name="fade">
      <div v-if="show" class="block-menu__overlay" @click.self="$emit('close')">
        <Transition name="scale">
          <div v-if="show" class="block-menu">
            <!-- Search -->
            <div class="block-menu__search">
              <v-icon icon="mdi-magnify" size="18" class="block-menu__search-icon" />
              <input
                ref="searchInput"
                v-model="search"
                type="text"
                class="block-menu__search-input"
                placeholder="Поиск блоков..."
                @keydown.down.prevent="navigateDown"
                @keydown.up.prevent="navigateUp"
                @keydown.enter.prevent="selectCurrent"
                @keydown.escape="$emit('close')"
              />
            </div>

            <!-- Blocks list -->
            <div class="block-menu__list">
              <div v-if="filteredBlocks.length === 0" class="block-menu__empty">
                Ничего не найдено
              </div>

              <!-- Basic blocks -->
              <div v-if="filteredFreeBlocks.length > 0" class="block-menu__section">
                <div class="block-menu__section-title">Основные</div>
                <button
                  v-for="(block, index) in filteredFreeBlocks"
                  :key="block.blockType"
                  class="block-menu__item"
                  :class="{ 'block-menu__item--active': selectedIndex === index }"
                  @click="select(block)"
                  @mouseenter="selectedIndex = index"
                >
                  <div class="block-menu__item-icon">
                    <v-icon :icon="getIcon(block.blockType)" size="20" />
                  </div>
                  <div class="block-menu__item-info">
                    <span class="block-menu__item-name">{{ block.blockName }}</span>
                    <span class="block-menu__item-desc">{{ getDescription(block.blockType) }}</span>
                  </div>
                </button>
              </div>

              <!-- Premium blocks -->
              <div v-if="filteredPremiumBlocks.length > 0" class="block-menu__section">
                <div class="block-menu__section-title">
                  Премиум
                  <span v-if="!isPremium" class="block-menu__pro-badge">PRO</span>
                </div>
                <button
                  v-for="(block, index) in filteredPremiumBlocks"
                  :key="block.blockType"
                  class="block-menu__item"
                  :class="{
                    'block-menu__item--active': selectedIndex === filteredFreeBlocks.length + index,
                    'block-menu__item--locked': !isPremium
                  }"
                  @click="select(block)"
                  @mouseenter="selectedIndex = filteredFreeBlocks.length + index"
                >
                  <div class="block-menu__item-icon" :class="{ 'block-menu__item-icon--premium': !isPremium }">
                    <v-icon :icon="getIcon(block.blockType)" size="20" />
                  </div>
                  <div class="block-menu__item-info">
                    <span class="block-menu__item-name">{{ block.blockName }}</span>
                    <span class="block-menu__item-desc">{{ getDescription(block.blockType) }}</span>
                  </div>
                  <v-icon v-if="!isPremium" icon="mdi-lock" size="14" class="block-menu__item-lock" />
                </button>
              </div>
            </div>

            <!-- Upgrade hint -->
            <button v-if="!isPremium && filteredPremiumBlocks.length > 0" class="block-menu__upgrade" @click="goToPlans">
              <v-icon icon="mdi-crown" size="16" />
              <span>Разблокировать все блоки</span>
              <v-icon icon="mdi-arrow-right" size="16" />
            </button>
          </div>
        </Transition>
      </div>
    </Transition>
  </Teleport>
</template>

<script>
const blockIcons = {
  TEXT: 'mdi-text',
  HEADING: 'mdi-format-header-1',
  DIVIDER: 'mdi-minus',
  CHECKLIST: 'mdi-checkbox-marked-outline',
  BULLET_LIST: 'mdi-format-list-bulleted',
  KEY_VALUE: 'mdi-table-row',
  IDEA_CARD: 'mdi-lightbulb-outline',
  PROS: 'mdi-thumb-up-outline',
  CONS: 'mdi-thumb-down-outline',
  LINK: 'mdi-link-variant',
  STATUS: 'mdi-flag-outline',
  QUOTE: 'mdi-format-quote-close',
}

const blockDescriptions = {
  TEXT: 'Простой текстовый блок',
  HEADING: 'Заголовок H1, H2, H3',
  DIVIDER: 'Горизонтальный разделитель',
  CHECKLIST: 'Список задач с галочками',
  BULLET_LIST: 'Маркированный список',
  KEY_VALUE: 'Пары ключ-значение',
  IDEA_CARD: 'Структурированная идея',
  PROS: 'Преимущество / плюс',
  CONS: 'Недостаток / минус',
  LINK: 'Ссылка на ресурс',
  STATUS: 'Статус задачи',
  QUOTE: 'Цитата с автором',
}

export default {
  name: 'BlockTypeSelector',
  props: {
    show: {
      type: Boolean,
      default: false,
    },
    blockTypes: {
      type: Array,
      default: () => [],
    },
    isPremium: {
      type: Boolean,
      default: false,
    },
  },
  emits: ['close', 'select'],
  data() {
    return {
      search: '',
      selectedIndex: 0,
    }
  },
  computed: {
    filteredBlocks() {
      if (!this.search.trim()) return this.blockTypes
      const q = this.search.toLowerCase()
      return this.blockTypes.filter(b =>
        b.blockName.toLowerCase().includes(q) ||
        this.getDescription(b.blockType).toLowerCase().includes(q)
      )
    },
    filteredFreeBlocks() {
      return this.filteredBlocks.filter(b => b.isFreeBlock)
    },
    filteredPremiumBlocks() {
      return this.filteredBlocks.filter(b => !b.isFreeBlock)
    },
  },
  watch: {
    show(val) {
      if (val) {
        this.search = ''
        this.selectedIndex = 0
        this.$nextTick(() => {
          this.$refs.searchInput?.focus()
        })
      }
    },
    search() {
      this.selectedIndex = 0
    },
  },
  methods: {
    getIcon(type) {
      return blockIcons[type] || 'mdi-card-outline'
    },
    getDescription(type) {
      return blockDescriptions[type] || ''
    },
    select(block) {
      if (!block.isFreeBlock && !this.isPremium) {
        this.goToPlans()
        return
      }
      this.$emit('select', block.blockType)
    },
    navigateDown() {
      const total = this.filteredBlocks.length
      if (total > 0) {
        this.selectedIndex = (this.selectedIndex + 1) % total
      }
    },
    navigateUp() {
      const total = this.filteredBlocks.length
      if (total > 0) {
        this.selectedIndex = (this.selectedIndex - 1 + total) % total
      }
    },
    selectCurrent() {
      const block = this.filteredBlocks[this.selectedIndex]
      if (block) {
        this.select(block)
      }
    },
    goToPlans() {
      this.$emit('close')
      this.$router.push({ name: 'subscription-plans' })
    },
  },
}
</script>

