<template>
  <div class="pros-block" :class="{ 'pros-block--editing': editing }">
    <div v-if="!editing" class="pros-block__view" @click="startEdit">
      <div class="pros-block__icon-wrapper">
        <v-icon icon="mdi-thumb-up" size="18" />
      </div>
      <p class="pros-block__content">{{ block.content || 'Нажмите для редактирования...' }}</p>
      <button class="pros-block__menu" @click.stop="$emit('delete')">
        <v-icon icon="mdi-dots-vertical" size="18" />
      </button>
    </div>
    <div v-else class="pros-block__edit">
      <div class="pros-block__icon-wrapper">
        <v-icon icon="mdi-thumb-up" size="18" />
      </div>
      <textarea
        ref="textarea"
        v-model="draft"
        class="pros-block__textarea"
        placeholder="Введите преимущество..."
        rows="2"
        @blur="save"
        @keydown.escape="cancel"
      />
    </div>
  </div>
</template>

<script>
import { BlocksCall } from '@/api/call'

export default {
  name: 'ProsBlock',
  props: {
    block: {
      type: Object,
      required: true,
    },
    pageId: {
      type: String,
      required: true,
    },
  },
  emits: ['update', 'delete'],
  data() {
    return {
      editing: false,
      draft: '',
    }
  },
  methods: {
    startEdit() {
      this.draft = this.block.content || ''
      this.editing = true
      this.$nextTick(() => {
        this.$refs.textarea?.focus()
      })
    },
    async save() {
      if (this.draft === this.block.content) {
        this.editing = false
        return
      }

      try {
        await BlocksCall.updateBlock(this.pageId, this.block.id, {
          type: 'PROS',
          textContent: this.draft,
        })
        this.$emit('update', { ...this.block, content: this.draft })
      } catch (error) {
        console.error('Failed to update block:', error)
      }
      this.editing = false
    },
    cancel() {
      this.editing = false
    },
  },
}
</script>

