<template>
  <div class="link-block" :class="{ 'link-block--editing': editing }">
    <div v-if="!editing" class="link-block__view">
      <div class="link-block__icon-wrapper" @click="openLink">
        <v-icon icon="mdi-link-variant" size="20" />
      </div>
      <div class="link-block__content" @click="startEdit">
        <span class="link-block__text">{{ block.text || block.url || 'Добавить ссылку' }}</span>
        <span v-if="block.url" class="link-block__url">{{ displayUrl }}</span>
      </div>
      <button class="link-block__menu" @click="$emit('delete')">
        <v-icon icon="mdi-dots-vertical" size="18" />
      </button>
    </div>
    <div v-else class="link-block__edit">
      <input
        ref="urlInput"
        v-model="draftUrl"
        type="url"
        class="link-block__input"
        placeholder="https://example.com"
        @keyup.enter="$refs.textInput?.focus()"
      />
      <input
        ref="textInput"
        v-model="draftText"
        type="text"
        class="link-block__input"
        placeholder="Текст ссылки"
        @keyup.enter="save"
        @blur="save"
      />
      <button class="link-block__cancel" @click="cancel">
        <v-icon icon="mdi-close" size="18" />
      </button>
    </div>
  </div>
</template>

<script>
import { BlocksCall } from '@/api/call'

export default {
  name: 'LinkBlock',
  props: {
    block: {
      type: Object,
      required: true,
    },
    pageId: {
      type: String,
      required: true,
    },
  },
  emits: ['update', 'delete'],
  data() {
    return {
      editing: false,
      draftUrl: '',
      draftText: '',
    }
  },
  computed: {
    displayUrl() {
      if (!this.block.url) return ''
      try {
        const url = new URL(this.block.url)
        return url.hostname
      } catch {
        return this.block.url
      }
    },
  },
  methods: {
    startEdit() {
      this.draftUrl = this.block.url || ''
      this.draftText = this.block.text || ''
      this.editing = true
      this.$nextTick(() => {
        this.$refs.urlInput?.focus()
      })
    },
    openLink() {
      if (this.block.url) {
        window.open(this.block.url, '_blank')
      } else {
        this.startEdit()
      }
    },
    async save() {
      if (this.draftUrl === this.block.url && this.draftText === this.block.text) {
        this.editing = false
        return
      }

      try {
        await BlocksCall.updateBlock(this.pageId, this.block.id, {
          type: 'LINK',
          url: this.draftUrl,
          text: this.draftText,
        })
        this.$emit('update', { ...this.block, url: this.draftUrl, text: this.draftText })
      } catch (error) {
        console.error('Failed to update link:', error)
      }
      this.editing = false
    },
    cancel() {
      this.editing = false
    },
  },
}
</script>

