<template>
  <div class="idea-block">
    <div class="idea-block__header">
      <v-icon icon="mdi-lightbulb-outline" size="18" class="idea-block__icon" />
      <span class="idea-block__label">Карточка идеи</span>
      <button class="idea-block__menu" @click="$emit('delete')">
        <v-icon icon="mdi-dots-vertical" size="18" />
      </button>
    </div>

    <div class="idea-block__fields">
      <div class="idea-block__field">
        <label class="idea-block__field-label">Проблема</label>
        <textarea
          v-model="draft.problem"
          class="idea-block__textarea"
          placeholder="Какую проблему решает?"
          rows="2"
          @blur="save"
        />
      </div>

      <div class="idea-block__field">
        <label class="idea-block__field-label">Решение</label>
        <textarea
          v-model="draft.solution"
          class="idea-block__textarea"
          placeholder="Как решить проблему?"
          rows="2"
          @blur="save"
        />
      </div>

      <div class="idea-block__field">
        <label class="idea-block__field-label">Аудитория</label>
        <input
          v-model="draft.audience"
          type="text"
          class="idea-block__input"
          placeholder="Целевая аудитория"
          @blur="save"
        />
      </div>

      <div class="idea-block__field">
        <label class="idea-block__field-label">MVP</label>
        <textarea
          v-model="draft.mvp"
          class="idea-block__textarea"
          placeholder="Минимальный продукт"
          rows="2"
          @blur="save"
        />
      </div>

      <div class="idea-block__field">
        <label class="idea-block__field-label">Монетизация</label>
        <input
          v-model="draft.monetization"
          type="text"
          class="idea-block__input"
          placeholder="Как заработать?"
          @blur="save"
        />
      </div>
    </div>
  </div>
</template>

<script>
import { BlocksCall } from '@/api/call'

export default {
  name: 'IdeaCardBlock',
  props: {
    block: {
      type: Object,
      required: true,
    },
    pageId: {
      type: String,
      required: true,
    },
  },
  emits: ['update', 'delete'],
  data() {
    return {
      draft: {
        problem: '',
        solution: '',
        audience: '',
        mvp: '',
        monetization: '',
      },
    }
  },
  watch: {
    block: {
      immediate: true,
      handler(val) {
        this.draft = {
          problem: val.problem || '',
          solution: val.solution || '',
          audience: val.audience || '',
          mvp: val.mvp || '',
          monetization: val.monetization || '',
        }
      },
    },
  },
  methods: {
    async save() {
      const changed =
        this.draft.problem !== this.block.problem ||
        this.draft.solution !== this.block.solution ||
        this.draft.audience !== this.block.audience ||
        this.draft.mvp !== this.block.mvp ||
        this.draft.monetization !== this.block.monetization

      if (!changed) return

      try {
        await BlocksCall.updateBlock(this.pageId, this.block.id, {
          type: 'IDEA_CARD',
          ...this.draft,
        })
        this.$emit('update', { ...this.block, ...this.draft })
      } catch (error) {
        console.error('Failed to update idea:', error)
      }
    },
  },
}
</script>

