<template>
  <div class="bullet-block">
    <div class="bullet-block__header">
      <v-icon icon="mdi-format-list-bulleted" size="18" class="bullet-block__icon" />
      <span class="bullet-block__label">Список</span>
      <button class="bullet-block__menu" @click="$emit('delete')">
        <v-icon icon="mdi-dots-vertical" size="18" />
      </button>
    </div>

    <div class="bullet-block__items">
      <div
        v-for="(item, index) in items"
        :key="index"
        class="bullet-block__item"
      >
        <span class="bullet-block__bullet"></span>
        <input
          v-model="items[index]"
          type="text"
          class="bullet-block__input"
          placeholder="Новый пункт..."
          @blur="saveItems"
          @keyup.enter="addItem(index + 1)"
        />
        <button
          v-if="items.length > 1"
          class="bullet-block__remove"
          @click="removeItem(index)"
        >
          <v-icon icon="mdi-close" size="16" />
        </button>
      </div>
    </div>

    <button class="bullet-block__add" @click="addItem()">
      <v-icon icon="mdi-plus" size="18" />
      Добавить пункт
    </button>
  </div>
</template>

<script>
import { BlocksCall } from '@/api/call'

export default {
  name: 'BulletListBlock',
  props: {
    block: {
      type: Object,
      required: true,
    },
    pageId: {
      type: String,
      required: true,
    },
  },
  emits: ['update', 'delete'],
  data() {
    return {
      items: [],
    }
  },
  watch: {
    block: {
      immediate: true,
      handler(val) {
        this.items = val.items?.length ? [...val.items] : ['']
      },
    },
  },
  methods: {
    addItem(atIndex) {
      const idx = atIndex ?? this.items.length
      this.items.splice(idx, 0, '')
      this.$nextTick(() => {
        const inputs = this.$el.querySelectorAll('.bullet-block__input')
        inputs[idx]?.focus()
      })
    },
    async removeItem(index) {
      this.items.splice(index, 1)
      if (this.items.length === 0) {
        this.items.push('')
      }
      await this.saveItems()
    },
    async saveItems() {
      try {
        await BlocksCall.updateBlock(this.pageId, this.block.id, {
          type: 'BULLET_LIST',
          items: this.items,
        })
        this.$emit('update', { ...this.block, items: [...this.items] })
      } catch (error) {
        console.error('Failed to update bullet list:', error)
      }
    },
  },
}
</script>

