<template>
  <div class="status-block">
    <div class="status-block__header">
      <v-icon icon="mdi-flag-outline" size="18" class="status-block__icon" />
      <span class="status-block__label">Статус</span>
      <button class="status-block__menu" @click="$emit('delete')">
        <v-icon icon="mdi-dots-vertical" size="18" />
      </button>
    </div>

    <div class="status-block__options">
      <button
        v-for="option in statusOptions"
        :key="option.value"
        class="status-block__option"
        :class="{
          'status-block__option--active': block.status === option.value,
          ['status-block__option--' + option.value.toLowerCase()]: true
        }"
        @click="setStatus(option.value)"
      >
        <v-icon :icon="option.icon" size="18" />
        <span>{{ option.label }}</span>
      </button>
    </div>
  </div>
</template>

<script>
import { BlocksCall } from '@/api/call'

export default {
  name: 'StatusBlock',
  props: {
    block: {
      type: Object,
      required: true,
    },
    pageId: {
      type: String,
      required: true,
    },
  },
  emits: ['update', 'delete'],
  data() {
    return {
      statusOptions: [
        { value: 'IDEA', label: 'Идея', icon: 'mdi-lightbulb-outline' },
        { value: 'IN_PROGRESS', label: 'В процессе', icon: 'mdi-progress-clock' },
        { value: 'DONE', label: 'Готово', icon: 'mdi-check-circle-outline' },
        { value: 'DROPPED', label: 'Отменено', icon: 'mdi-close-circle-outline' },
      ],
    }
  },
  methods: {
    async setStatus(status) {
      if (status === this.block.status) return

      try {
        await BlocksCall.updateBlock(this.pageId, this.block.id, {
          type: 'STATUS',
          status,
        })
        this.$emit('update', { ...this.block, status })
      } catch (error) {
        console.error('Failed to update status:', error)
      }
    },
  },
}
</script>

