  width: 32px;
  height: 32px;
  border-radius: 8px;
  background: rgba(255, 255, 255, 0.04);
  border: none;
  color: #8b8b9e;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.2s ease;
}

.status-block__menu:active {
  transform: scale(0.92);
  background: rgba(239, 68, 68, 0.15);
  color: #ef4444;
}

@media (hover: hover) {
  .status-block__menu {
    opacity: 0;
    background: transparent;
  }

  .status-block:hover .status-block__menu {
    opacity: 1;
  }

  .status-block__menu:hover {
    color: #ef4444;
    background: rgba(239, 68, 68, 0.1);
  }
}

.status-block__options {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 8px;
}

.status-block__option {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 10px 12px;
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.08);
  background: rgba(255, 255, 255, 0.03);
  color: #8b8b9e;
  font-size: 13px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
}

.status-block__option:active {
  transform: scale(0.98);
}

/* IDEA - Blue */
.status-block__option--idea.status-block__option--active {
  background: rgba(59, 130, 246, 0.15);
  border-color: rgba(59, 130, 246, 0.4);
  color: #3b82f6;
}

/* IN_PROGRESS - Yellow */
.status-block__option--in_progress.status-block__option--active {
  background: rgba(245, 158, 11, 0.15);
  border-color: rgba(245, 158, 11, 0.4);
  color: #f59e0b;
}

/* DONE - Green */
.status-block__option--done.status-block__option--active {
  background: rgba(74, 222, 128, 0.15);
  border-color: rgba(74, 222, 128, 0.4);
  color: #4ade80;
}

/* DROPPED - Gray */
.status-block__option--dropped.status-block__option--active {
  background: rgba(100, 100, 120, 0.15);
