<template>
  <div class="quote-block" :class="{ 'quote-block--editing': editing }">
    <div v-if="!editing" class="quote-block__view" @click="startEdit">
      <div class="quote-block__content">
        <v-icon icon="mdi-format-quote-open" size="24" class="quote-block__quote-icon" />
        <p class="quote-block__text">{{ block.text || 'Нажмите для добавления цитаты...' }}</p>
        <span v-if="block.author" class="quote-block__author">— {{ block.author }}</span>
      </div>
      <button class="quote-block__menu" @click.stop="$emit('delete')">
        <v-icon icon="mdi-dots-vertical" size="18" />
      </button>
    </div>
    <div v-else class="quote-block__edit">
      <textarea
        ref="textarea"
        v-model="draftText"
        class="quote-block__textarea"
        placeholder="Текст цитаты..."
        rows="3"
      />
      <input
        v-model="draftAuthor"
        type="text"
        class="quote-block__input"
        placeholder="Автор цитаты"
        @blur="save"
        @keyup.enter="save"
      />
      <button class="quote-block__cancel" @click="cancel">
        <v-icon icon="mdi-close" size="18" />
      </button>
    </div>
  </div>
</template>

<script>
import { BlocksCall } from '@/api/call'

export default {
  name: 'QuoteBlock',
  props: {
    block: {
      type: Object,
      required: true,
    },
    pageId: {
      type: String,
      required: true,
    },
  },
  emits: ['update', 'delete'],
  data() {
    return {
      editing: false,
      draftText: '',
      draftAuthor: '',
    }
  },
  methods: {
    startEdit() {
      this.draftText = this.block.text || ''
      this.draftAuthor = this.block.author || ''
      this.editing = true
      this.$nextTick(() => {
        this.$refs.textarea?.focus()
      })
    },
    async save() {
      if (this.draftText === this.block.text && this.draftAuthor === this.block.author) {
        this.editing = false
        return
      }

      try {
        await BlocksCall.updateBlock(this.pageId, this.block.id, {
          type: 'QUOTE',
          text: this.draftText,
          author: this.draftAuthor,
        })
        this.$emit('update', { ...this.block, text: this.draftText, author: this.draftAuthor })
      } catch (error) {
        console.error('Failed to update quote:', error)
      }
      this.editing = false
    },
    cancel() {
      this.editing = false
    },
  },
}
</script>

