<template>
  <div class="checklist-block">
    <div class="checklist-block__header">
      <v-icon icon="mdi-checkbox-marked-outline" size="18" class="checklist-block__icon" />
      <span class="checklist-block__label">Чек-лист</span>
      <button class="checklist-block__menu" @click="$emit('delete')">
        <v-icon icon="mdi-dots-vertical" size="18" />
      </button>
    </div>

    <div class="checklist-block__items">
      <div
        v-for="(item, index) in items"
        :key="index"
        class="checklist-block__item"
      >
        <button
          class="checklist-block__checkbox"
          :class="{ 'checklist-block__checkbox--checked': item.status }"
          @click="toggleItem(index)"
        >
          <v-icon
            :icon="item.status ? 'mdi-checkbox-marked' : 'mdi-checkbox-blank-outline'"
            size="22"
          />
        </button>
        <input
          v-model="item.text"
          type="text"
          class="checklist-block__input"
          :class="{ 'checklist-block__input--done': item.status }"
          placeholder="Новый пункт..."
          @blur="saveItems"
          @keyup.enter="addItem(index + 1)"
        />
        <button
          v-if="items.length > 1"
          class="checklist-block__remove"
          @click="removeItem(index)"
        >
          <v-icon icon="mdi-close" size="16" />
        </button>
      </div>
    </div>

    <button class="checklist-block__add" @click="addItem()">
      <v-icon icon="mdi-plus" size="18" />
      Добавить пункт
    </button>
  </div>
</template>

<script>
import { BlocksCall } from '@/api/call'

export default {
  name: 'ChecklistBlock',
  props: {
    block: {
      type: Object,
      required: true,
    },
    pageId: {
      type: String,
      required: true,
    },
  },
  emits: ['update', 'delete'],
  data() {
    return {
      items: [],
    }
  },
  watch: {
    block: {
      immediate: true,
      handler(val) {
        this.items = val.items?.map(i => ({ text: i.text, status: i.checked })) || [{ text: '', status: false }]
      },
    },
  },
  methods: {
    async toggleItem(index) {
      this.items[index].status = !this.items[index].status
      await this.saveItems()
    },
    addItem(atIndex) {
      const idx = atIndex ?? this.items.length
      this.items.splice(idx, 0, { text: '', status: false })
      this.$nextTick(() => {
        const inputs = this.$el.querySelectorAll('.checklist-block__input')
        inputs[idx]?.focus()
      })
    },
    async removeItem(index) {
      this.items.splice(index, 1)
      if (this.items.length === 0) {
        this.items.push({ text: '', status: false })
      }
      await this.saveItems()
    },
    async saveItems() {
      try {
        await BlocksCall.updateBlock(this.pageId, this.block.id, {
          type: 'CHECKLIST',
          items: this.items.map(i => ({ text: i.text, checked: i.status })),
        })
        this.$emit('update', { ...this.block, items: this.items.map(i => ({ text: i.text, checked: i.status })) })
      } catch (error) {
        console.error('Failed to update checklist:', error)
      }
    },
  },
}
</script>

