<template>
  <Teleport to="body">
    <Transition name="fade">
      <div v-if="show" class="confirm-dialog__overlay" @click.self="$emit('cancel')">
        <Transition name="slide-up">
          <div v-if="show" class="confirm-dialog">
            <h3 class="confirm-dialog__title">{{ title }}</h3>
            <p v-if="message" class="confirm-dialog__message">{{ message }}</p>
            <div class="confirm-dialog__actions">
              <button
                class="confirm-dialog__btn confirm-dialog__btn--cancel"
                @click="$emit('cancel')"
              >
                {{ cancelText }}
              </button>
              <button
                class="confirm-dialog__btn"
                :class="{ 'confirm-dialog__btn--danger': danger }"
                @click="$emit('confirm')"
              >
                {{ confirmText }}
              </button>
            </div>
          </div>
        </Transition>
      </div>
    </Transition>
  </Teleport>
</template>

<script>
export default {
  name: 'ConfirmDialog',
  props: {
    show: {
      type: Boolean,
      default: false,
    },
    title: {
      type: String,
      required: true,
    },
    message: {
      type: String,
      default: '',
    },
    confirmText: {
      type: String,
      default: 'Удалить',
    },
    cancelText: {
      type: String,
      default: 'Отмена',
    },
    danger: {
      type: Boolean,
      default: false,
    },
  },
  emits: ['confirm', 'cancel'],
}
</script>

