<template>
  <div class="kv-block">
    <div class="kv-block__header">
      <v-icon icon="mdi-table-row" size="18" class="kv-block__icon" />
      <span class="kv-block__label">Ключ-значение</span>
      <button class="kv-block__menu" @click="$emit('delete')">
        <v-icon icon="mdi-dots-vertical" size="18" />
      </button>
    </div>

    <div class="kv-block__items">
      <div
        v-for="(item, index) in items"
        :key="index"
        class="kv-block__item"
      >
        <input
          v-model="item.key"
          type="text"
          class="kv-block__key"
          placeholder="Ключ"
          @blur="saveItems"
        />
        <input
          v-model="item.value"
          type="text"
          class="kv-block__value"
          placeholder="Значение"
          @blur="saveItems"
          @keyup.enter="addItem(index + 1)"
        />
        <button
          v-if="items.length > 1"
          class="kv-block__remove"
          @click="removeItem(index)"
        >
          <v-icon icon="mdi-close" size="16" />
        </button>
      </div>
    </div>

    <button class="kv-block__add" @click="addItem()">
      <v-icon icon="mdi-plus" size="18" />
      Добавить пару
    </button>
  </div>
</template>

<script>
import { BlocksCall } from '@/api/call'

export default {
  name: 'KeyValueBlock',
  props: {
    block: {
      type: Object,
      required: true,
    },
    pageId: {
      type: String,
      required: true,
    },
  },
  emits: ['update', 'delete'],
  data() {
    return {
      items: [],
    }
  },
  watch: {
    block: {
      immediate: true,
      handler(val) {
        this.items = val.items?.map(i => ({ ...i })) || [{ key: '', value: '' }]
      },
    },
  },
  methods: {
    addItem(atIndex) {
      const idx = atIndex ?? this.items.length
      this.items.splice(idx, 0, { key: '', value: '' })
      this.$nextTick(() => {
        const keys = this.$el.querySelectorAll('.kv-block__key')
        keys[idx]?.focus()
      })
    },
    async removeItem(index) {
      this.items.splice(index, 1)
      if (this.items.length === 0) {
        this.items.push({ key: '', value: '' })
      }
      await this.saveItems()
    },
    async saveItems() {
      try {
        await BlocksCall.updateBlock(this.pageId, this.block.id, {
          type: 'KEY_VALUE',
          items: this.items.map(i => ({ key: i.key, value: i.value })),
        })
        this.$emit('update', { ...this.block, items: [...this.items] })
      } catch (error) {
        console.error('Failed to update key-value:', error)
      }
    },
  },
}
</script>

